@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#logo1, #logo2, #logo3, #logo4 {
  height: 150px;
  width: 300px;
}

.project-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#proj1, #proj2, #proj3, #proj4 {
  flex: 1 1;
}

.skills-div {
  display: flex;
  flex-direction: row;
}

#languages, #management, #methods, #databases {
  flex: 1 1;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#about-info {
  margin: 10px;
  width: 50%;
  text-align: center;

}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.nav {
  display: flex;
  flex-direction: row;
}

#name {
  font-family: "Montserrat", sans-serif;
  margin-left: 20px;
}

.nav-item {
  margin: 5px;
}
.footer {
  /* background-color: #2c9ad5; */
}

#footer-text {
  margin: 2px;
  color: black;
  font-size: 12px;
  text-decoration: none;
}
.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1 1;
  /* background-color: #2c9ad5; */
  padding-bottom: 40px;
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

